* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-size: 35px;
    font-family: "Rubik"
}

body {
    background-color: #132043;
    color: white;
}

/**================== *The Header ================*/

header {
    width: 100%;
    background-color: #279EFF;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 100;
}

header::before {
    content: "";
    width: 100%;
    height: 100%;
    background-color: #132043;
    box-shadow: 1px 1px 1px 1px white;
    position: absolute;
    right: 0;
    top: 0;
    animation: theAnimation 1.5s 1s ease-in-out forwards 1;
}

@keyframes theAnimation {
    0% {
        width: 100%;
    }

    100% {
        width: 0%;
    }
}

header img {
    width: 45px;
    height: 45px;
    aspect-ratio: 16 / 9;
    border-radius: 50%;
    cursor: pointer;
    margin-bottom: -5px;
    margin-left: 60px;
}

header nav a {
    font-weight: bold;
    font-size: 1.3em;
    text-decoration: none;
    color: #fff;
    margin-right: 60px;
}

/* *====================================== */

main {
    margin-top: 130px;
}

main h1 {
    margin-bottom: 20px;
    position: relative;
    font-size: 1.2em;
    text-align: center;
}

main h1::before {
    content: "";
    width: 100%;
    height: 100%;
    background-color: #132043;
    position: absolute;
    right: 0;
    top: 0;
    animation: theAnimation 1s 3s ease-in-out forwards 1;
}

/* *===============* Search input ===============*/

.search-box {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-block: 10px;
}

.search-box::before {
    content: "";
    width: 100%;
    height: 100%;
    background-color: #132043;
    position: absolute;
    right: 0;
    top: 0;
    animation: theAnimation 1s 4s ease-in-out forwards 1;
    z-index: 1;
}

.search-box input {
    padding: 7px 5px 7px 10px;
    border: none;
    outline: none;
    border-radius: 5px;
    position: relative;
}

.search-box button {
    padding: 7px 10px;
    border-radius: 10px;
    cursor: pointer;
    margin-left: 10px;
    border: none;
    outline: none;
    background-color: #279EFF;
    color: #fff;
    box-shadow: 1px 1px 2px 2px #fff;
    transition: .5s;
}

.search-box button:hover {
    box-shadow: none;
}

/* *=============================================*/

.btns-box {
    margin-block: 45px;
    margin-inline: auto;
    width: 95%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
}

.btns-box::before {
    content: "";
    width: 100%;
    height: 100%;
    background-color: #132043;
    position: absolute;
    right: 0;
    top: 0;
    animation: theAnimation 1s 6s ease-in-out forwards 1;
}

.btns-box button {
    padding-block: 5px;
    width: 150px;
    cursor: pointer;
    border-radius: 10px;
    border: 3px solid gray;
    font-weight: 600;
    color: #fff;
    background-color: transparent;
    transition: .5s;
    font-size: 0.6rem;
}

.toggleColorBtn {
    border-color: white;
}

.btns-box button:hover {
    border-color: white;
}

.container {
    width: 100%;
    display: grid;
    grid-template-columns: auto auto;
    gap: 25px;
    padding: 10px;
    align-items: center;
    justify-content: center;
    position: relative;
}

.container::before {
    content: "";
    width: 100%;
    height: 100%;
    background-color: #132043;
    position: absolute;
    right: 0;
    bottom: 0;
    animation: container-animation 4s 7s ease-in-out forwards 1;
    z-index: 20;
}

@keyframes container-animation {
    0% {
        height: 100%;
    }

    100% {
        height: 0%;
    }
}

.container .image-box {
    position: relative;
}

.container .image-box img {
    width: 100%;
    filter: grayscale(100%);
    transition: .4s;
}

.container .image-box:hover img {
    filter: none;
}

.image-box .author {
    position: absolute;
    top: 6px;
    left: 6px;
    transition: .5s;
    z-index: 1;
    transform: scale(0);
    background-color: #132043;
    padding-inline: 5px;
    border-radius: 10px;
    border: 2px solid transparent;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    font-size: 0.5em;
    padding: 4px 6px;
}

.image-box .author:hover {
    opacity: 0.9;
    border: 2px solid #fff;
}

.image-box #downloadBtn {
    text-decoration: none;
    position: absolute;
    bottom: 15px;
    right: 10px;
    background-color: black;
    padding: 7px;
    border-radius: 5px;
    transition: .5s;
    transform: scale(0);
    z-index: 2;
    cursor: pointer;
    font-size: 0.6em;
    border: 2px solid transparent;
}

.image-box #downloadBtn::before {
    content: "Download";
    position: absolute;
    top: -37px;
    left: 50%;
    background-color: black;
    transform: translateX(-50%);
    padding: 5px 10px;
    border-radius: 10px;
    display: none;
    font-size: 0.6em;
}

.image-box #downloadBtn::after {
    content: "";
    width: 12px;
    height: 12px;
    background-color: black;
    position: absolute;
    top: -13px;
    left: 50%;
    transform: rotate(45deg) translateX(-50%);
    display: none;
    z-index: -1;
}

.image-box #downloadBtn:hover {
    opacity: 0.7;
    border: 2px solid #fff;
}

.image-box #downloadBtn:hover::before,
.image-box #downloadBtn:hover::after {
    display: block;
}

.image-box #downloadBtn i {
    color: #fff;
    text-shadow: 1px 1px 1px black, -1px -1px 1px black;
}

.image-box .author a {
    font-weight: bold;
    text-decoration: none;
    color: white;
    font-size: 0.9em;
    margin-left: 5px;
}

.image-box .author i {
    font-size: 25px;
}

.image-box:hover .author {
    transform: scale(1);
}

.image-box:hover #downloadBtn {
    transform: scale(1);
}

/* *=============The footer: =============== */

footer {
    width: 100%;
    padding-block: 10px;
    background-color: #279EFF;
    display: flex;
    align-items: center;
    justify-content: space-around;
    position: relative;
}

footer::before {
    content: "";
    width: 100%;
    height: 100%;
    background-color: #132043;
    position: absolute;
    right: 0;
    top: 0;
    animation: theAnimation 1.5s 11s ease-in-out forwards;
    z-index: 22;
}

footer .social-media {
    width: 300px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

footer .social-media span a {
    border: 3px solid #fff;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px;
    position: relative;
    overflow: hidden;
    z-index: 1;
}

footer .social-media a::before {
    content: "";
    width: 0%;
    height: 100%;
    background-color: #132043;
    top: 0;
    left: 0;
    position: absolute;
    transition: .7s;
    z-index: -1;

}

footer .social-media a {
    color: #fff;
    text-decoration: none;
}

footer .social-media a:hover::before {
    width: 100%;
}

footer .social-media a i {
    color: #fff;
    font-size: 0.9em;
}

footer .copyright p {
    font-size: 0.9em;
}

/* *============ not found result ========== */
.noResults-box {
    padding: 20px;
    background-color: red;
    border-radius: 5px;
    margin-block: 20px;
    position: relative;
}

.noResults-box p {
    margin-block: 10px;
}

.noResults-box::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    background-color: red;
    width: 20px;
    height: 20px;
}

.noResults-box #error-logo {
    position: absolute;
    top: -56px;
    left: 50%;
    transform: translateX(-50%);
    font-size: 60px;
    color: red;
}

/* *======== Media Query for phone: ==========*/
@media screen and (max-width: 768px) {
    * {
        font-size: 27px;
    }

    header img {
        margin-left: 15px;
    }

    header nav a {
        margin-right: 15px;
    }

    .container {
        grid-template-columns: auto;
    }

    .btns-box {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        justify-content: center;
    }

    .btns-box button {
        padding-inline: 10px;
        margin: 10px;
    }

    .search-box {
        margin-inline: auto;
        width: 95%;
        flex-direction: column;
        row-gap: 10px;
    }

    .image-box #downloadBtn::before {
        top: -33px;
    }

    .search-box input {
        width: 97%;
    }

    .search-box button {
        padding: 4px 8px;
    }

    footer {
        flex-direction: column;
        row-gap: 15px;
    }
}